import React from "react";
import { transformJsonToPages } from "../../../common/functions";

/*
  Import file.json to ImageData format
*/

export class ImportButton extends React.Component {
  constructor(props) {
    super(props)
    this.state = props.state
    this.methods = this.props.methods
  }

  fileImport(e) {
    this.methods.successfullyImport()

    let file = e.target.files[0],
        reader = new FileReader(),
        _this = this

    let types = ['application/json'];
    if (file) {
      if (types.includes(file.type)) {
        reader.onload = function(event) {
          _this.methods.afterImport( transformJsonToPages(event.target.result) )
        }
        
        reader.readAsText(file)
      } else {
        console.warn('Неверный тип файла')
      }
    }
  }

  render() {
    return <input 
      className="import" 
      id="import" 
      type="file"
      onChange={(e) => this.fileImport(e)}></input>
  }
}