import React from "react";

/*
  Functional of figures
*/

export class _Figures extends React.Component {

  adapSelectingArea(figureName, x1, y1, x2, y2) {
    switch(figureName) {
      case 'triangle':
        return { x1, y1, x2: x2 + (x2 - x1), y2 }
      case 'arbitraryTriangle':
        return {x1, y1, x2: x2 + (x2 - x1) * 3, y2}
      case 'rhombus':
        return {x1, y1: y1 - (y2 - y1), x2: x2 + (x2 - x1), y2}
      case 'parallelogram':
        if ([1, 4].includes(this.adapQuarter(x1, y1, x2, y2, 14).quater)) {
          return {x1: x1 - 40, y1,  x2: x2 + 40, y2}
        } else {
          return {x1, y1, x2, y2}
        }
      case 'circle':
        return {x1: x2 - (x2 - x1), y1: y2 - (x2 - x1), x2: x2 + (x2 - x1), y2:  y2 + (x2 - x1)}
      case 'ellipse':
        return {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)}
      case 'coordinatesSystem':
        return {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)}
      default: return {x1, y1, x2, y2}
    }
  }

  onMouseUpEvent(state, x1, y1, x2, y2, memoryResave) {
    let { ctx, instruments } = state

    let resaveItem = selectedAreaCoordinates => {
      return {
        active: true,
        type: instruments.figure.selected,
        types: instruments.figure.types,
        canvasId: state.activeCanvasId,
        color: state.globalColor,
        fillColor: instruments.figure.settings.fillColor,
        dashedMode: instruments.figure.settings.dashedMode,
        coordinates: {
          figure: {x1, y1, x2, y2},
          selectedArea: selectedAreaCoordinates
        }
      }
    }

    switch(true) {
      case instruments.figure.types.triangle:
        // Triangle 
        this.selectingArea(ctx, x1, y1, x2 + (x2 - x1), y2)
        memoryResave(resaveItem({ x1, y1, x2: x2 + (x2 - x1), y2 }))
        break
      case instruments.figure.types.rightTriangle:
        // Right Triangle
        this.selectingArea(ctx, x1, y1, x2, y2)
        memoryResave(resaveItem( {x1, y1, x2, y2} ))
        break
      case instruments.figure.types.arbitraryTriangle:
        // Arbitrary Triangle
        this.selectingArea(ctx, x1, y1, x2 + (x2 - x1) * 3, y2)
        memoryResave(resaveItem( {x1, y1, x2: x2 + (x2 - x1) * 3, y2} ))
        break
      case instruments.figure.types.rhombus:
        // Rhombus
        this.selectingArea(ctx, x1, y1 - (y2 - y1),  x2 + (x2 - x1), y2)
        memoryResave(resaveItem( {x1, y1: y1 - (y2 - y1), x2: x2 + (x2 - x1), y2} ))
        break
      case instruments.figure.types.rectangle:
        // Rectangle
        this.selectingArea(ctx, x1, y1, x2, y2)
        memoryResave(resaveItem( {x1, y1, x2, y2} ))
        break
      case instruments.figure.types.parallelogram:
        // Parallelogram
        if ([1, 4].includes(this.adapQuarter(x1, y1, x2, y2, 14).quater)) {
          this.selectingArea(ctx, x1 - 40, y1, x2 + 40, y2)
          memoryResave(resaveItem( {x1: x1 - 40, y1,  x2: x2 + 40, y2} ))
        } else {
          this.selectingArea(ctx, x1, y1, x2, y2)
          memoryResave(resaveItem( {x1, y1, x2, y2} ))
        }
        break
      case instruments.figure.types.circle:
        // Circle
        this.selectingArea(ctx, x2 - (x2 - x1), y2 - (x2 - x1), x2 + (x2 - x1), y2 + (x2 - x1))
        memoryResave(resaveItem( {x1: x2 - (x2 - x1), y1: y2 - (x2 - x1), x2: x2 + (x2 - x1), y2:  y2 + (x2 - x1)} ))
        break
      case instruments.figure.types.ellipse:
        // Ellipse
        this.selectingArea(ctx, x2 - (x2 - x1), y2 - (y2 - y1), x2 + (x2 - x1), y2 + (y2 - y1))
        memoryResave(resaveItem( {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)} ))
        break
      case instruments.figure.types.coordinatesSystem:
        // Coordinates System
        this.selectingArea(ctx, x2 - (x2 - x1), y2 - (y2 - y1), x2 + (x2 - x1), y2 + (y2 - y1))
        memoryResave(resaveItem( {x1: x2 - (x2 - x1), y1: y2 - (y2 - y1), x2: x2 + (x2 - x1), y2: y2 + (y2 - y1)} ))
        break
      default:;
    }
  }

  drawFigureSelectArea(figure, state, ctx) {
    let { x1, y1, x2, y2 } = figure.coordinates.selectedArea

    this.selectingArea(ctx, x1, y1, x2, y2)
  }

  setStyles(state) {
    let { ctx, instruments, globalColor } = state
    
    // Common border style
    ctx.strokeStyle = globalColor
    ctx.lineJoin = 'round'
    ctx.lineCap = 'round'
    ctx.lineWidth = 1
    instruments.figure.settings.dashedMode ?  ctx.setLineDash([5, 15]) :  ctx.setLineDash([])

    if (instruments.figure.settings.fillColor) {
      if (!instruments.figure.types.coordinatesSystem) {
        ctx.fillStyle = instruments.figure.settings.fillColor
        ctx.fill()
      }
    }
  }
  
  drawFigure(state, x, y, x2, y2) {
    let { ctx, instruments } = state

    switch(true) {
      case instruments.figure.types.triangle:
        // Triangle 
        this.triangle(ctx, x, y, x2, y2)
        break
      case instruments.figure.types.arbitraryTriangle:
        // Arbitrary Triangle
        this.arbitraryTriangle(ctx, x, y, x2, y2)
        break
      case instruments.figure.types.rightTriangle:
        // Right Triangle
        this.rightTriangle(ctx, x, y, x2, y2)
        break
      case instruments.figure.types.rhombus:
        // Rhombus
        this.rhombus(ctx, x, y, x2, y2)
        break
      case instruments.figure.types.rectangle:
        // Rectangle
        this.rectangle(ctx, x, y, x2, y2)
        break
      case instruments.figure.types.parallelogram:
        // Parallelogram
        this.parallelogram(ctx, x, y, x2, y2)
        break
      case instruments.figure.types.circle:
        // Circle
        this.circle(ctx, x, y, x2, y2)
        break
      case instruments.figure.types.ellipse:
        // Ellipse
        this.ellipse(ctx, instruments, x, y, x2, y2)
        break
      case instruments.figure.types.coordinatesSystem:
        // Coordinates System
        this.coordinatesSystem(ctx, x, y, x2, y2)
        break
      default:;
    }
  }

  selectingArea(ctx, x1, y1, x2, y2) {
    ctx.setLineDash([14, 15])
    ctx.strokeStyle = '#019267'

    // Adding padding of figure
    let w = 14
    let [n1, n2, n3, n4] = this.adapQuarter(x1, y1, x2, y2, w).numbers
    this.rectangle(ctx, x1 + n1, y1 + n2, x2 + n3, y2 + n4)
    // this.resizeRectangle(ctx, x2 + 7, y1 - 7)
  }

  triangle(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineTo(x2, y2)
    ctx.lineTo(x2 + (x2 - x1), y1)
    ctx.closePath()
    ctx.stroke()
  }

  arbitraryTriangle(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineTo(x2, y2)
    ctx.lineTo(x2 + (x2 - x1) * 3, y1)
    ctx.closePath()
    ctx.stroke()
  }

  rightTriangle(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineTo(x2, y1)
    ctx.lineTo(x2, y2)
    ctx.closePath()
    ctx.stroke()
  }

  rhombus(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineTo(x2, y2)
    ctx.lineTo(x2 + (x2 - x1), y1)
    ctx.lineTo(x2, y1 - (y2 - y1) )
    ctx.closePath()
    ctx.stroke()
  }

  rectangle(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineTo(x2, y1)
    ctx.lineTo(x2, y2)
    ctx.lineTo(x1, y2)
    ctx.lineTo(x1, y1)
    ctx.stroke()
  }

  resizeRectangle(ctx, x, y) {
    // State restore
    ctx.restore()

    ctx.setLineDash([])
    ctx.strokeStyle = '#019267'
    this.rectangle(ctx, x, y, x + 14, y - 14)
    ctx.fillStyle = '#019267'
    ctx.fill()
  }

  parallelogram(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.moveTo(x1, y1)
    ctx.lineTo(x2 + 40, y1)
    ctx.lineTo(x2, y2)
    ctx.lineTo(x1 - 40, y2)
    ctx.lineTo(x1, y1)
    ctx.stroke()
  }  
  
  circle(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    ctx.arc(x2, y2, Math.abs(x2 - x1), 0, Math.PI * 2);
    ctx.stroke()
  }

  ellipse(ctx, instruments, x1, y1, x2, y2) {
    // Draw the ellipse
    ctx.beginPath()
    ctx.ellipse(x2, y2, Math.abs(x2 - x1), Math.abs(y2 - y1), Math.PI, 0, 2 * Math.PI)
    ctx.stroke()

    // Draw the ellipse's line of reflection
    // ctx.beginPath()
    // ctx.setLineDash([7, 10])
    
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(Math.abs(x2 - x + x2 + ((Math.abs(x2 - x + x2)) / 10)), y2);
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(Math.abs(x2 + x - x2 - ((Math.abs(x2 - x + x2)) / 10)), y2);
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 + y - y2) - ((Math.abs(y2 + y - y2)) / 3));
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 - y + y2) + ((Math.abs(y2 + y - y2)) / 4));
    
    // Lines insides
    // ctx.moveTo(x2, y2)
    // ctx.lineTo(Math.abs(x2 - x + x2), y2)
    // ctx.moveTo(x2, y2)
    // ctx.lineTo(Math.abs(x2 + x - x2), y2)
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 + y - y2));
    // ctx.moveTo(x2, y2);
    // ctx.lineTo(x2, Math.abs(y2 - y + y2));
    ctx.stroke();
  }

  coordinatesSystem(ctx, x1, y1, x2, y2) {
    ctx.beginPath()
    let size = 7

    ctx.moveTo(x2, y2)
    ctx.lineTo(Math.abs(x2 - x1 + x2), y2)
    ctx.lineTo(Math.abs(x2 - x1 + x2 - size), y2 + size)
    ctx.moveTo(Math.abs(x2 - x1 + x2), y2)
    ctx.lineTo(Math.abs(x2 - x1 + x2 - size), y2 - size)
    
    ctx.moveTo(x2, y2)
    ctx.lineTo(Math.abs(x2 + x1 - x2), y2)
    
    ctx.moveTo(x2, y2)
    ctx.lineTo(x2, Math.abs(y2 + y1 - y2))
    ctx.lineTo(x2 - size, Math.abs(y2 + y1 - y2 + size))
    ctx.moveTo(x2, Math.abs(y2 + y1 - y2))
    ctx.lineTo(x2 + size, Math.abs(y2 + y1 - y2 + size))
    
    ctx.moveTo(x2, y2)
    ctx.lineTo(x2, Math.abs(y2 - y1 + y2))
    
    ctx.stroke()
  }

  adapQuarter(x, y, x1, y1, width) {
    let w = Math.abs(width)
    
    return {
      numbers:  x1 > x ? ( y1 > y ? [-w, -w, w, w] : [-w, w, w, -w] ) : ( y1 > y ? [w, -w, -w, w] : [w, w, -w, -w]),
      quater:  x1 > x ? ( y1 > y ? 4 : 1) : ( y1 > y ? 3 : 2)
    }

    // if (x < x1 && y > y1) {
    //   return [-w, w, w, -w]
    // }

    // if (x > x1 && y > y1) {
    //   return [w, w, -w, -w]
    // } 
    
    // if (x > x1 && y < y1) {
    //   return [w, -w, -w, w]
    // }

    // if (x < x1 && y < y1) {
    //   return [-w, -w, w, w]
    // }
  }
}