import React from "react";
import { imgs } from "../../common/imgs";
import { ExportButton } from "./components/ExportButton";
import { ImportButton } from "./components/ImportButton";
import { PagesList } from "./components/PagesList";
import { PDFdownloadButton } from "./components/PDFdownloadButton";

class Possibilities extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      author: false
    }
  }
  
  cleanUpPage() {
    if (window.confirm('Уверены, что хотите очистить страницу?')) {
      this.props.methods.cleanUpPage()
    }
  }

  fullPage() {
    this.props.methods.navigationToggle()
  }

  showAuthor() {
    this.setState({ author: !this.state.author })
  }

  render() {
    return <div className="possibilities">
      <div className="possibilitie" title="Отменить последнее действие" onClick={(e) => this.props.methods.returnPreviousAction()}>
        <img src={imgs.returnImg} alt="return"/>
      </div>
      <div className="possibilitie" title="Очистить страницу" onClick={() => this.cleanUpPage()}>
        <img src={imgs.trashCan} alt="trashCan"/>
      </div>
      <PDFdownloadButton parentState={this.props.parentState} />
      <PagesList 
        methods={{
          displayedPagesResave: this.props.methods.displayedPagesResave,
          changePage: this.props.methods.changePage,
          createNewPage: this.props.methods.createNewPage
        }}
        parentState={this.props.parentState}/>
      <div className="cover import-functional export-functional">
        <ExportButton
          parentState={this.props.parentState}
          methods={{successfullyExport: this.props.methods.successfullyExport}}/>
        <label title="Импорт проекта" htmlFor="import">ИМПОРТ</label>
        <ImportButton 
           methods={{
            successfullyImport: this.props.methods.successfullyImport,
            afterImport: this.props.methods.afterImport
          }}/>
      </div>
      {/* <div className="cover">
        <div className="possibilitie" title="На весь экран" onClick={() => this.fullPage()}>
        <img src={imgs.fullPage} alt="fullPage"/>
        </div>
      </div> */}
      <div className="cover author">
        {this.state.author && <div className="author-block">
          <p>Версия: <span>2.2.1</span></p>    
          <p>Дата обновления: <span>25.05.2022</span></p>    
          <p>Разработчик: <a target="_blank" href="https://www.linkedin.com/in/ivan-alesik-b118121b6">@alesikivan</a></p>  
        </div>}
        <div 
          className={`possibilitie ${this.state.author ? 'active' : ''}`} 
          title="Разработчик проекта" 
          onClick={() => this.showAuthor()}>
          <img src={imgs.info} alt="fullPage"/>
        </div>
      </div> 
    </div>
  }
}

export default Possibilities