import React from "react";
import { downloadFile, transformPagesToJson } from "../../../common/functions";

/*
  Transform ImageData format to file.json and export new file
*/

export class ExportButton extends React.Component {
  constructor(props) {
    super(props)
    this.methods = this.props.methods
  }

  fileExport() {
    this.props.methods.successfullyExport()
    
    let copy = transformPagesToJson(this.props.parentState)
    let file = new Blob([copy], {type: 'application/json'})
    
    downloadFile(file)
  }

  render() {
    return <div 
      className="possibilitie export" 
      title="Экспорт проекта" 
      onClick={(e) => this.fileExport(e)}>
      <span className="item">ЭКСПОРТ</span>
  </div>
  }
}