import jsPDF from "jspdf";
import React from "react";
import { generateCanvas } from "../../../common/functions";

/*
  Transform ImageData format to pdf and save the file
*/

export class PDFdownloadButton extends React.Component {
  constructor(props) {
    super(props)
    this.state = props.state
    this.methods = this.props.methods
  }

  saveToPDF() {
    let { pages, canvas: {width, height} } = this.props.parentState

    let pdf = new jsPDF(
      (width > height ? 'l' : 'p'), 'px', 
      (width > height ? [width, height] : [height, width]))

    pages.forEach((page, idx) => {
      let item = page.history[page.history.length - 1]
      let _canvas = generateCanvas(item.width, item.height)
      let ctx = _canvas.getContext('2d')
      ctx.putImageData(item, 0, 0)

      // Brush share line to PDF lists
      this.addSharelLine(item, ctx, '#ccc')

      width = pdf.internal.pageSize.getWidth()
      height = pdf.internal.pageSize.getHeight()

      pdf.addImage(_canvas, 'PNG', 0, 0, width, height)

      if (idx + 1 < pages.length)
        pdf.addPage(width > height ? 'l' : 'p')
    })

    pdf.save('project.pdf')
  }

  addSharelLine(doc, ctx, color) {
    ctx.beginPath()    
    ctx.strokeStyle = color
    ctx.moveTo(0, doc.height - 1)   
    ctx.lineTo(doc.width - 1, doc.height - 1)
    ctx.stroke()
  }

  render() {
    return <div className="possibilitie" title="Сохранить страницу" onClick={(e) => this.saveToPDF(e)}>
    <span className="item">PDF</span>
  </div>
  }
}